import { showToast, Toast } from "@raycast/api";
import {
  scanDirectory,
  analyzeFiles,
  type ScanOptions,
  type AnalyzeFilesOptions,
} from "tidyf";
import { getAvailableModels } from "tidyf";

import * as path from "path";
import * as os from "os";

export type {
  FileMetadata,
  OrganizationProposal,
  FileMoveProposal,
} from "tidyf";

type Provider = { id: string; models?: unknown };
type ModelsResponse = { data?: { providers?: Provider[] } };

/**
 * Wraps the getAvailableModels function with Raycast error handling
 */
export async function safeGetAvailableModels() {
  try {
    const response = await getAvailableModels();

    // The response structure from opencode seems to be { data: { providers: [...] } }
    const providers = (response as ModelsResponse).data?.providers;
    const opencodeProvider = providers?.find((p) => p.id === "opencode");

    if (!opencodeProvider || !opencodeProvider.models) {
      return [];
    }

    // Convert models object/array to standardized format for Raycast dropdown
    // Models can be an array of strings or an object where keys are model IDs
    const models = opencodeProvider.models;
    let modelList: { id: string; name: string }[] = [];

    if (Array.isArray(models)) {
      modelList = models.map((id: string) => ({ id, name: id }));
    } else if (typeof models === "object") {
      modelList = Object.keys(models).map((id) => ({ id, name: id }));
    }

    return modelList;
  } catch (error) {
    await showToast({
      style: Toast.Style.Failure,
      title: "Failed to fetch models",
      message: error instanceof Error ? error.message : String(error),
    });
    return [];
  }
}

/**
 * Wraps the scanDirectory function with Raycast error handling
 */
export async function safeScanDirectory(
  dirPath: string,
  options: ScanOptions = {},
) {
  try {
    return await scanDirectory(dirPath, options);
  } catch (error) {
    await showToast({
      style: Toast.Style.Failure,
      title: "Scan Failed",
      message: error instanceof Error ? error.message : String(error),
    });
    throw error;
  }
}

/**
 * Wraps the analyzeFiles function with Raycast progress feedback
 */
export async function safeAnalyzeFiles(options: AnalyzeFilesOptions) {
  try {
    return await analyzeFiles(options);
  } catch (error) {
    if (
      error instanceof Error &&
      (error.message.includes("Connection refused") ||
        error.message.includes("opencode"))
    ) {
      await showToast({
        style: Toast.Style.Failure,
        title: "Opencode Server Not Found",
        message: "Please run 'opencode server' in your terminal.",
      });
    } else {
      await showToast({
        style: Toast.Style.Failure,
        title: "Analysis Failed",
        message: error instanceof Error ? error.message : String(error),
      });
    }
    throw error;
  }
}

/**
 * Helper to get user's home directory
 */
export function getHomeDir() {
  return os.homedir();
}

/**
 * Helper to resolve common paths (Downloads, Desktop)
 */
export function resolvePath(shortPath: string) {
  if (shortPath.startsWith("~")) {
    return path.join(getHomeDir(), shortPath.slice(1));
  }
  return shortPath;
}
